import React, { Component } from "react";

export default class Home extends Component {
  constructor(props) {
    super(props);

    this.balance = this.balance.bind(this);
    this.inventario = this.inventario.bind(this);
  }

  async componentDidMount() {
    setInterval(() => {
      this.balance();
      this.inventario();
    }, 3 * 1000);
  }

  async balance() {
    document.getElementById("getValue").innerHTML =
      await this.props.wallet.contractToken.methods
        .balanceOf(this.props.currentAccount)
        .call({ from: this.props.currentAccount });
  }

  inventario() {
    document.getElementById("inventory").innerHTML = "";

    this.props.wallet.contractMarket.methods
      .largoInventario(this.props.currentAccount)
      .call({ from: this.props.currentAccount })
      .then(function (result) {
        for (let index = 0; index < result; index++) {
          this.props.wallet.contractMarket.methods
            .inventario(this.props.currentAccount, index)
            .call({ from: this.props.currentAccount })
            .then(function (item) {
              const div = document.createElement("div");
              div.className = "col-lg-4 col-md-12 p-1";

              const img = document.createElement("img");
              img.className = "pb-4";

              img.src = "assets/img/" + item.nombre + ".png";

              img.setAttribute("width", "100%");

              div.appendChild(img);
              document.getElementById("inventory").appendChild(div);
            });
        }
      });
  }

  render() {
    return (
      <>
        <header className="masthead text-center text-white">
          <div className="masthead-content">
            <div className="container px-5">
              <div className="row">
                <div className="col-lg-12 col-md-12 p-4 text-center">
                  <h2 className=" pb-4">Coin Packs</h2>
                </div>

                <div className="col-lg-4 col-md-12 p-4 text-center monedas">
                  <h2 className=" pb-4">BASIC</h2>
                  <img
                    className=" pb-4"
                    src="assets/img/01.png"
                    width="100%"
                    alt=""
                  />
                  <div
                    className="position-relative btn-monedas"
                    onClick="javascript:void(0)"
                  >
                    <span className="position-absolute top-50 end-0 translate-middle-y p-5">
                      soon...
                    </span>
                  </div>
                </div>

                <div className="col-lg-4 col-md-12 p-4 monedas">
                  <h2 className=" pb-4">PREMIUM</h2>
                  <img
                    className=" pb-4"
                    src="assets/img/02.png"
                    width="100%"
                    alt=""
                  />
                  <div
                    className="position-relative btn-monedas"
                    onClick="javascript:void(0)"
                  >
                    <span className="position-absolute top-50 end-0 translate-middle-y p-5">
                      soon...
                    </span>
                  </div>
                </div>

                <div className="col-lg-4 col-md-12 p-4 monedas">
                  <h2 className=" pb-4">GOLD</h2>
                  <img
                    className=" pb-4"
                    src="assets/img/03.png"
                    width="100%"
                    alt=""
                  />
                  <div
                    className="position-relative btn-monedas"
                    onClick="javascript:void(0)"
                  >
                    <span className="position-absolute top-50 end-0 translate-middle-y p-5">
                      soon...
                    </span>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </header>

        <div className="container mt-3 mb-3">
          <div className="row text-center">
            <div className="col-md-12">
              <img
                id="metaicon"
                className="meta-gray"
                width="100"
                height="100"
                src="data:image/png;base64,"
                alt="logo metamask"
              />
            </div>
          </div>
          <div className="row text-center">
            <div className="col-md-12">
              <span id="status"></span>
            </div>
          </div>
          <div style={{ marginTop: "30px" }} className="row text-center">
            <div className="col-md-12">
              <h3>Account Info</h3>
              {this.props.currentAccount}
            </div>
          </div>
          <div className="row text-center">
            <div className="col-md-12">
              <span>
                Current balance: <i id="getValue">loading...</i>{" "}
                <button
                  className="btn btn-primary"
                  onClick={async () => this.balance()}
                >
                  Balance
                </button>
              </span>
            </div>
          </div>
          <hr />

          <div style={{ marginTop: "30px" }} className="row text-center">
            <div className="col-md-12">
              <h3>Account inventory</h3>{" "}
              <button
                className="btn btn-primary"
                onClick={() => this.inventario()}
              >
                {" "}
                refresh
              </button>
            </div>
          </div>

          <div className="row text-center" id="inventory"></div>
        </div>
      </>
    );
  }
}
